.newsletter
{
  width: 65%;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 150px;
  background: linear-gradient(180deg, #e85df1 0%, #69686822 60%);
  gap: 30px;
  border-radius: 50px;
}
.newsletter h1{
  color:#252525;
  font-size: 55px;
  font-weight: 600;
}
.newsletter p{
  color: #222222;
  font-size: 20px;
}
.newsletter div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  width: 730px;
  height: 70px;
  border-radius: 80px;
  border: 1px solid #e3e3e3;
}
.newsletter input{
  width: 500px;
  margin-left: 30px;
  border: none;
  outline: none;
  color: #616161;
  font-family: 'poppins';
  font-size: 16px;
}
.newsletter div button{
  width: 210px;
  height: 70px;
  border-radius: 80px;
  background: black;
  color: white;
  font-size: 16px;
  cursor: pointer;
}