.offers{
  width: 65%;
  height: 60vh;
  display: flex;
  margin: auto;
  padding: 0px 150px;
  margin-bottom: 150px;
  padding-bottom: 100px;
  border-radius: 50px;
  background: linear-gradient(180deg, #e85df1 0%, #69686822 60%);
}
.offers-left{
  flex:1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offers-left h1{
  color: #171717;
  font-size: 60px;
  font-weight: 600;
}
.offers-left p{
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button{
  width: 282px;
  height: 70px;
  border-radius: 35px;
  background: #FBE71F;
  border: none;
  color: black;
  font-size: 25px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}
.offers-right{
  flex:1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 60px;
}