.productdisplay{
  display: flex;
  margin: 0px 170px;
}
.productdisplay-left{
  display: flex;
  gap: 17px;
}
.productdisplay-img-list{
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.productdisplay-img-list img{
  height: 153px;
}
.productdisplay-main-img{
  width: 550px;
  height: 650px;
}
.productdisplay-right{
  display: flex;
  flex-direction: column;
  margin: 0px 70px;
}
.productdisplay-right h1{
  color: #3d3d3d;
  font-size: 40px;
  font-weight: 700;
}
.productdisplay-right-stars{
  display: flex;
  align-items: center;
  margin-top: 13px;
  gap: 5px;
  color: #1c1c1c;
  font-size: 16px;
}
.productdisplay-right-prices{
  display: flex;
  margin: 40px 0px;
  gap: 30px;
  font-size: 24px;
  font-weight: 700;
}
.productdisplay-right-prices-old{
  color: #818181;
  text-decoration: line-through;
}
.productdisplay-right-prices-new{
  color: #ff4141;
}
.productdisplay-right-size h1{
  margin-top: 55px;
  color: #656565;
  font-size: 20px;
  font-weight: 600;
}
.productdisplay-right-sizes{
  display: flex;
  margin: 30px 0px;
  gap: 20px;
}.productdisplay-right-sizes div{
  padding: 18px 24px;
  background: #f1f1f1;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  cursor: pointer;
}
.clk_but{
  padding: 20px 40px;
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: black;
  background: #FBE71F;
  margin-bottom: 40px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 50px;
  padding-bottom: 40px;
}
.productdisplay-right-category{
  margin-top: 10px;
}
.productdisplay-right span{
  font-weight: 600;
}